<template>
  <div class="login-wrap">
    <div class="loginForm">
      <div class="formItem">
        <van-field v-model="accountNo" placeholder="账号" :error-message="accountNoErr" required
          @keyup.enter.native="loginSubmit">
          <van-icon slot="left-icon" class-prefix="iconfont icon-wode-weixuanzhong" />
        </van-field>
      </div>

      <div class="formItem">
        <van-field v-model="password" type="password" placeholder="密码" :error-message="passwordErr"
          required>
          <van-icon slot="left-icon" class-prefix="iconfont icon-mima" />
        </van-field>
      </div>

      <div class="formItem">
        <van-field v-model="code" placeholder="验证码" :error-message="codeErr" required
          @keyup.enter.native="loginSubmit">
          <van-icon slot="left-icon" class-prefix="iconfont icon-yanzhengma" />
          <div slot="button">

            <van-image class="item-code" fit="contain" :src="codeImg" @click="getCode">
              <template v-slot:error>
                <van-loading v-if="loadCode" type="spinner" size="20" />
                <span v-else>加载失败</span>
              </template>
            </van-image>
          </div>
        </van-field>
      </div>
    </div>
    <div class="footer">
      <van-button type="primary" :loading="loading" @click="loginSubmit" :disabled="disabled"
        loading-text="登录中...">登录</van-button>
      <div class="footer_operate">
        <span></span>
        <span @click="showTypeChange(2)" v-if="!$store.getters.singleLoginUrl">用户注册</span>
        <span @click="toOther" v-else>
          其它登录方式：
          <van-icon name="newspaper-o" color="#07c160" size="6vw" @click="toOther" />
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import md5 from "js-md5";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { encrypt, decryption } from "@/utils/index";
import { LoginPlatform } from "@/utils/enum";
export default {
  name: "r-login",
  created() {},
  data() {
    return {
      accountNo: this.$store.getters.userName || "",
      accountNoErr: "",
      password: decryption(this.$store.getters.userPassword || "") || "",
      passwordErr: "",
      code: "",
      codeErr: "",
      codeID: "",
      disabled: false,
      codeImg: "",
      loading: false,
      loadCode: false
    };
  },
  mounted() {
    if (!this.$store.getters.serverIP) {
      this.showTypeChange(0);
    } else {
      this.getCode();
    }
  },
  methods: {
    getCode() {
      if (!this.$store.getters.serverIP) return;
      this.codeImg = "";
      this.loadCode = true;
      this.$mainHttp.account
        .getLoginCode()
        .then(res => {
          if (res.Result && res.Datas) {
            this.codeID = res.Datas.slice(0, 32);
            this.codeImg = `data:image/png;base64,${res.Datas.slice(32)}`;
          }
          this.loadCode = false;
        })
        .catch(err => {
          this.codeImg = "";
          this.loadCode = false;
        });
    },
    showTypeChange(val) {
      this.$emit("change", val);
    },
    loginSubmit() {
      this.accountNoErr = "";
      this.passwordErr = "";
      if (!this.$store.getters.serverIP) {
        this.$toast("请先设置服务地址");
        this.showTypeChange(0);
        return;
      }

      if (this.accountNo === "") {
        this.accountNoErr = "请输入登录账号";
        return;
      }
      if (this.password === "") {
        this.passwordErr = "请输入登录密码";
        return;
      }

      if (this.code === "") {
        this.codeErr = "请输入验证码,不区分大小写";
        return;
      }
      this.codeValidate();
    },
    codeValidate() {
      this.$mainHttp.account
        .postCodeValidate({ code: this.code }, this.codeID)
        .then(res => {
          if (res.Result) {
            this.LoginRequest();
          } else if (res.Info) {
            this.codeErr = res.Info;
          } else {
            this.codeErr = "验证码错误";
          }
        })
        .catch(err => {
          this.codeErr = "验证码错误";
        });
    },
    LoginRequest() {
      if (this.loading) return;
      this.loading = true;
      this.disabled = true;
      this.$store
        .dispatch("LoginByUser", {
          userInfo: {
            name: this.accountNo,
            pwd: md5(this.password), //.toUpperCase()
            code: this.code,
            from: window.webkit
              ? LoginPlatform.StudentIos
              : LoginPlatform.StudentAndroid
          },
          other: this.codeID
        })
        .then(res => {
          if (res.Result) {
            if (res.Student) {
              this.$store.commit("SET_USERPASSWORD", encrypt(this.password));
              this.$router.push("/index");
              sendToAPP(FuncType.hadLogin, "");
            } else {
              this.$toast("登录出错，请重试");
              this.getCode();
            }
          } else if (res.Info) {
            this.$toast("账号或密码错误");
            this.getCode();
          } else {
            this.$toast("请输入正确账号和密码");
            this.getCode();
          }
          this.disabled = false;
          this.loading = false;
        })
        .catch(err => {
          this.disabled = false;
          this.loading = false;
        });
    },

    // 统一登录平台
    toOther() {
      this.$store.commit("SET_SINGLE_LOGIN_REAL", 1);
      setTimeout(() => {
        this.showTypeChange(3);
      }, 1);
    }
  }
};
</script>

<style lang="less">
</style>



