import { getNowDateTime } from "@/utils/index";
import { LoginPlatform } from "@/utils/enum";

export default {
    data() {
        return {
            conn: null,
            reConnect: false,
            timeStamp: 0
        };
    },
    mounted() {
        this.$nextTick(() => {
            this.startSignalR();
            window.addEventListener('visibilitychange', this.pageWakeUp);
            this.$on("hook:destroyed", () => {
                window.removeEventListener("visibilitychange", this.pageWakeUp);
            });
        });
    },
    methods: {
        pageWakeUp() {
            let that = this;
            if (!document.hidden) {
                console.log(getNowDateTime(), "页面激活");
                that.reConnect = false;
                that.startSignalR();
            }
        },
        async openSocket() {
            if (!this.$store.getters.singleLoginReal) return;
            if (!this.$store.getters.serverIP || !this.$store.getters.singleLoginUrl) return;
            // eslint-disable-next-line no-undef
            this.conn = $.hubConnection("http://" + this.$store.getters.serverIP + ":" + this.$config.mainSignalRPort);
            window.HUB = this.conn.createHubProxy("TerminalHub");
            // 如果没有注册的话，Hubs的 OnConnected 方法将不会被调用，那么客户端的方法就不能被服务端调用（这容易埋坑，所以要提前注册方法）。
            this.bindEvent();

            const strValue = (window.webkit ? LoginPlatform.TeacherIos : LoginPlatform.TeacherAndroid) + "," + this.$store.getters.deviceID;
            this.conn.qs = { LoginInfo: strValue };

            this.conn.start().done(data => {
                window.HUB.invoke("QuitGroup", this.$store.getters.deviceID).catch(err => {
                    console.log("err：", err);
                });
                window.HUB.invoke("JoinGroup", this.$store.getters.deviceID).catch(err => {
                    console.log("err：", err);
                });
            }).fail(() => {
                console.error("single-signalR服务未能连接上！");
            });

            this.conn.disconnected(() => {
                console.log(getNowDateTime(), "single-signalR服务已断开");
            });

            // stateChanged：连接状态更改时引发。 提供的旧状态和新的状态 （连接、 已连接、 正在重新连接或已断开连接）。
            this.conn.stateChanged((data) => {
                // console.log("data:", data);
                if (data.newState !== 1) {
                    this.startSignalR();
                } else {
                    console.info("single-signalR服务已连接上");
                    if (this.$refs["Login"]) this.$refs["Login"].toOther();
                }
            });

            // this.conn.error(() => {
            //   console.log(getNowDateTime(), "single-signalR服务异常");
            // });
        },
        bindEvent() {
            window.HUB.on("ReciveLogin", value => {
                const result = typeof value === "string" ? JSON.parse(value) : value;
                console.log("ReciveLogin:", result);
                this.receiveLoginSuccess(result);
            });
        },
        /**
         * 跳转链接
         * @param toPath 即将跳转的地址
         */
        turnPath(toPath) {
            if (this.$route.path.indexOf(toPath) === -1) {
                this.$router.replace(toPath);
            }
        },
        startSignalR() {
            if (!this.$store.getters.singleLoginReal) return;
            if (!this.$store.getters.singleLoginUrl) return;
            if (!this.reConnect) {
                console.log(getNowDateTime(), "请求启动single-signalR");
                this.reConnect = true;
                this.reConnectServer();
            }
        },
        reConnectServer() {
            try {
                if (!this.$store.getters.singleLoginReal) return;
                if (!this.$store.getters.singleLoginUrl) return;
                if (!this.conn || this.conn.state !== 1) {
                    console.log(getNowDateTime(), "检测到single-signalR服务未连接");
                    this.openSocket();
                    setTimeout(() => {
                        this.reConnectServer();
                    }, 5000);
                } else {
                    this.reConnect = false;
                }
            } catch (e) {
                setTimeout(() => {
                    this.reConnectServer();
                }, 3000);
            }
        },
        /**
         * 第三方授权登录成功
         * @param data
         */
        receiveLoginSuccess(data) {
            if (this.$store.getters.userName !== data["UserInfo"]["UserName"]) {
                this.$store.commit("SET_NOWCOURSE", null);
                this.$store.commit("SET_NOWCOURSEID", null);
            }
            this.$store.commit("SET_TOKEN", data["Data"]);
            this.$store.commit("SET_KEY", data["UserInfo"]["UserID"]);
            this.$store.commit("SET_USER", data["UserInfo"]["UserName"]);

            this.$store.commit("SET_STUDENT", data["Student"]);
            this.$store.commit("SET_STUDENTID", data["Student"]["StudentID"]);
            this.$router.push("/index");
        }
    },
    watch: {
        "$store.getters.singleLoginReal"(newVal, oldVal) {
            if (newVal) {
                setTimeout(() => {
                    this.startSignalR();
                }, 50);
            } else {
                this.reConnect = false;
                if (this.conn) this.conn.stop(false, true);
            }
        }
    },
    destroyed() {
        if (this.conn) this.conn.stop(false, true);
    }
};



