<template>
  <div class="login-wrap">
    <div class="loginForm setForm">
      <div class="formItem">
        <van-field v-model="serverIP" placeholder="服务地址" :error-message="serverIPErr" required
          @keyup.enter.native="setSubmit">
          <van-icon slot="left-icon" class-prefix="iconfont icon-IP1" />
        </van-field>
      </div>
    </div>
    <div class="footer">
      <van-button type="primary" :loading="loading" @click="setSubmit" :disabled="disabled"
        loading-text="保存中...">{{btnName}}</van-button>
      <div class="footer_operate">
        <span v-show="$store.getters.serverIP" @click="showTypeChange(1)">返回</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "r-setting",
  components: {},
  created() {},
  data() {
    return {
      serverIP: this.$store.getters.serverIP || "",
      serverIPErr: "",
      btnName: "保存",
      disabled: false,
      loading: false
    };
  },
  methods: {
    showTypeChange(val) {
      this.$emit("change", val);
    },
    setSubmit() {
      try {
        this.serverIPErr = "";
        if (this.serverIP === "") {
          this.serverIPErr = "请输入服务地址";
          return;
        } else {
          if (this.loading) return;
          this.loading = true;
          this.disabled = true;
          this.$store.commit("SET_SERVERIP", this.serverIP);
          this.$toast("保存成功");
          this.disabled = false;
          this.loading = false;
          this.btnName = "重新保存";
          this.singleLoginConfigGet();
          // this.showTypeChange(1);
        }
      } catch (e) {
        this.disabled = false;
        this.loading = false;
      }
    },
    singleLoginConfigGet() {
      if (!this.$store.getters.serverIP) return;
      this.$mainHttp.resourse
        .singleLoginConfigGet()
        .then(res => {
          if (res.Result) {
            this.$store.commit(
              "SET_SINGLE_LOGIN_URL",
              res.Datas["UnifiedloginURL"]
            );
            this.$store.commit(
              "SET_SINGLE_LOG_OUT_URL",
              res.Datas["UnifiedloginOutUrl"]
            );
            // if (this.$store.getters.singleLoginUrl) this.$refs["Login"].toOther();
            if (this.$store.getters.singleLoginUrl) {
              this.showTypeChange(3);
            } else {
              this.showTypeChange(1);
            }
          } else if (res.Info) {
            this.$toast(res.Info);
            this.showTypeChange(1);
          } else {
            this.showTypeChange(1);
          }
        })
        .catch(() => {
          this.showTypeChange(1);
        });
    }
  }
};
</script>

<style lang="less">
.setForm {
  padding-top: 6vw;
}
</style>



