<template>
  <div class="login-wrap">
    <!--  退出登录页-->
    <iframe ref="iframe" v-show="logOutShow && logOutUrl"
      :style="{width:'0',height:'0', border: '0'}" :src="logOutUrl">
    </iframe>
    <!--  白屏页面动画-->
    <r-loading v-show="showType === 999"></r-loading>

    <template v-if="showType !== 0 && showType !==3 && showType !== 999">
      <div class="flex-between login-head">
        <div v-if="showType === 3" class="login-change" @click="changeLogin">切换</div>
        <span>&nbsp;</span>
        <div class="login_setting">
          <van-icon name="setting-o" @click="changeShow(0)" />
        </div>
      </div>
      <div v-if="showType !== 3" class="logo"></div>
    </template>

    <r-setting v-if="showType===0" @change="changeShow"></r-setting>
    <r-register v-else-if="showType===2" @change="changeShow"></r-register>
    <other-login :school-server="schoolServerUrl" v-else-if="showType === 3" @change="changeShow">
    </other-login>

    <r-login ref="Login" v-show="showType === 1" @change="changeShow"></r-login>

    <van-dialog v-model="dialogShow" title="服务协议和隐私政策" show-confirm-button confirm-button-text="同意"
      show-cancel-button cancel-button-text="暂不使用" @confirm="agree" @cancel="cancel">
      <div class="dialog_message">
        <div>
          请你务必审慎阅读、充分理解“服务协议”和“隐私政策”各条款，包括但不限于：为了向你提供即时通讯、内容分享等服务，我们需要收集你的设备信息、操作日志等个人信息。你可以在“设置”中查看、变更、删除个人信息并管理你的授权。
        </div>
        <div>
          你可阅读
          <span class="dialog_operate" @click="showAgreement">《服务协议》</span>
          和
          <span class="dialog_operate" @click="showPolicy">《隐私政策》</span>
          了解详细信息。如你同意，请点击“同意”开始接受我们的服务。
        </div>
      </div>
    </van-dialog>

    <div class="footer-main"
      v-show="showType !== 3 && initHeight <= windowHeight && showType !== 999">
      <span>版本v{{version}} @睿和科技</span>
    </div>
  </div>
</template>

<script>
import RLoading from "@/components/r-loading";
import RLogin from "./components/R-Login";
import RRegister from "./components/R-Register";
import RSetting from "./components/R-Setting";
import OtherLogin from "./components/OtherLogin";
import config from "./../../../package.json";
import singleSignalR from "@/mixin/singleSignalR";
import { Dialog } from "vant";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { newGuid } from "@/utils/index";
import { LoginPlatform } from "@/utils/enum";

export default {
  name: "login",
  components: {
    RLoading,
    RLogin,
    RRegister,
    RSetting,
    OtherLogin,
    [Dialog.Component.name]: Dialog.Component
  },
  mixins: [singleSignalR],
  data() {
    return {
      showType: 999, // 0设置，1登录，2注册， 3第三方登录  999空白
      dialogShow: false,
      agreementVisible: false,
      policyVisible: false,

      initHeight: window.innerHeight,
      windowHeight: window.innerHeight,
      logOutShow: true,
      version: config.version
    };
  },
  computed: {
    schoolServerUrl() {
      return `${this.$store.getters.singleLoginUrl}/Interact.aspx?id=${
        this.$store.getters.deviceID
      }&type=${
        window.webkit ? LoginPlatform.StudentIos : LoginPlatform.StudentAndroid
      }&job=1`;
    },
    logOutUrl() {
      if (this.$store.getters.singleLogOutUrl) {
        return `${this.$store.getters.singleLogOutUrl}?adr=${
          this.$store.getters.singleLoginUrl
        }/Interact.aspx?id=${this.$store.getters.deviceID}&type=${
          window.webkit
            ? LoginPlatform.TeacherIos
            : LoginPlatform.TeacherAndroid
        }&job=1`;
      } else {
        return "";
      }
    },
    serverLinked() {
      return this.conn && this.conn.state !== 0;
    }
  },
  beforeCreate() {
    if (!this.$store.getters.deviceID) {
      this.$store.commit("SET_DEVICE_ID", newGuid());
    }
    this.$store.dispatch("logout");
  },
  created() {
    if (!this.$store.getters.singleLoginUrl) this.changeShow(1);
  },
  mounted() {
    if (!this.$store.getters.agreeUse) {
      this.dialogShow = true;
    }
    let that = this;
    window.onresize = () => {
      that.windowHeight = window.innerHeight;
    };
    let pasteTime = 0;
    if (this.$store.getters.singleLogOutUrl) pasteTime = 1000;
    setTimeout(() => {
      this.logOutShow = false;
      if (this.$store.getters.singleLoginUrl) this.changeShow(3);
    }, pasteTime);
  },
  methods: {
    changeShow(val) {
      this.showType = val;
      switch (this.showType) {
        case 1:
          if(this.$refs["Login"]) this.$refs["Login"].getCode();
          break;
        case 3:
          this.$store.commit("SET_SINGLE_LOGIN_REAL", 1);
          break;
      }
    },
    agree() {
      this.$store.commit("SET_AGREEUSE", 1);
    },
    cancel() {
      sendToAPP(FuncType.exitAPP, "");
    },
    showAgreement() {
      this.$router.push("/agreement");
    },
    showPolicy() {
      this.$router.push("/policy");
    },

    // singleLoginConfigGet() {
    //   if (!this.$store.getters.serverIP) return;
    //   this.$mainHttp.resourse
    //     .singleLoginConfigGet()
    //     .then(res => {
    //       if (res.Result) {
    //         this.$store.commit(
    //           "SET_SINGLE_LOGIN_URL",
    //           res.Datas["UnifiedloginURL"]
    //         );
    //         this.$store.commit(
    //           "SET_SINGLE_LOG_OUT_URL",
    //           res.Datas["UnifiedloginOutUrl"]
    //         );
    //         if (this.$store.getters.singleLoginUrl)
    //           this.$refs["Login"].toOther();
    //       } else if (res.Info) {
    //         this.$toast(res.Info);
    //       } else {
    //       }
    //     })
    //     .catch(() => {});
    // },
    changeLogin() {
      this.$store.commit("SET_SINGLE_LOGIN_REAL", 0);
      this.changeShow(1);
    }
  },
  watch: {
    // "$store.getters.serverIP": {
    //   immediate: true,
    //   handler: "singleLoginConfigGet"
    // }
  }
};
</script>

<style lang="less">
// @import url("./../../styles/common/base.less");
@font-title: 18px;
@font-sub-title: 16px;
@font-info: 14px;
.login-wrap {
  width: 100%;
  height: 100%;
  background: url("./../../assets/bg_login.png");
  background-size: cover;
  overflow: hidden;
  position: relative;
  .login-head {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    font-size: 5vw;
    line-height: 44px;

    .login-change {
      padding-left: 2vw;
      font-size: @font-sub-title;
      color: #409eff;
    }

    .login_setting {
      padding-right: 2vw;
    }
  }

  .logo {
    width: 28vw;
    height: 120px;
    background: url("./../../assets/logo.png") center;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 9vw 36vw;
  }
  .loginForm {
    // width: 100%;
    margin: 0 15vw;
    text-align: left;
    .formItem {
      width: 100%;
      margin-bottom: 12px;
      .iconfont {
        font-size: @font-sub-title;
        margin-right: 1vw;
        color: #999;
      }
      input {
        font-size: @font-sub-title;
        color: #999;
        padding-left: 6px;
        border: none;
        border-bottom: 1px solid rgba(153, 153, 153, 1);
        box-sizing: border-box;
      }
      .item-code {
        width: 100px;
      }
    }
  }
  .footer {
    margin-top: 2vw;
    button {
      min-width: 66vw;
      background: rgba(0, 203, 171, 1);
      border-radius: 5px;
      border: 1px solid #00cbab;
    }

    .footer_operate {
      padding: 10px 17vw;
      display: flex;
      justify-content: flex-end;
      color: #409eff;
      font-size: @font-sub-title;
    }
  }

  .dialog_message {
    padding: 12px 20px;
    text-align: left;
  }
  .dialog_operate {
    color: #409eff;
  }

  .footer-main {
    position: absolute;
    bottom: 16px;
    left: 50%;
    transform: translateX(-50%);
    width: fit-content;
  }
}
</style>



